<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-9 col-md-6 col-sm-6 col-6">
            <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
          </div>
          <div class="col-lg-3 col-md-6 col-sm-6 col-6">
            <column-toggle
              v-model="columnDefs"
              @input="cols => this.$refs['table'].updateColumns(cols)"
            ></column-toggle>
          </div>
        </div>
        <div class="col-sm-12 p-0">
          <my-table
            ref="table"
            :columnDefs="columnDefs"
            v-bind:data-rows="comments"
            :actions="actions"
            @sortChange="sortChange"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import commentSchmas from './comment-schemas'
  import dtHelper from 'src/helpers/datatable'
  import ColumnToggle from 'src/components/UIComponents/ColumnToggle'

  export default{
    components: {
      MyTable,
      MyFilter,
      ColumnToggle
    },
    computed: {
      comments() {
        let rows = this.$store.state.comment;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      let initFiledArrays = ['id', 'title', 'blog_name', 'view', 'created_at', 'status'];
      let columnDefs = dtHelper.buildInitFields(commentSchmas, initFiledArrays);

      return {
        filterOutput: [],
        columnDefs: columnDefs,
        actions: [],
        filter: {},
        rules: dtHelper.buildRules(commentSchmas)
      }
    },
    mounted() {
      this.$store.dispatch('fetchComment');

      this.$store.dispatch('setPageTitle', 'bình luận');
    },
    methods: {
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchComment', {
          order: prop + '=' + order
        });
      },
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
